import { Injectable } from "@angular/core";
import { Http, Response, Headers, RequestOptions } from "@angular/http";
import { NgRedux, select } from "@angular-redux/store";
import { Observable } from "rxjs/Rx";
import * as _ from "lodash";

import { StatusNotificationService } from "../../AppModule/services/status-notification.service";
import {
  GoogleSheetsService,
  CompanyInformation,
  StockListHeader,
  StockAnalysisService,
  DecoratedYahooQuote,
  YahooDataService,
  YahooFinanceResult,
  YahooQuote,
  IAppState,
  Actions
} from "../../../shared";
import { VixModel, TableViewState } from "../models";

@Injectable()
export class RestInteractorService {
  @select("tableState") public tableViewState$: Observable<TableViewState>;

  private stockListHeaders: StockListHeader[];
  private currentList: string;
  constructor(
    private ngRedux: NgRedux<IAppState>,
    private statusNotificationService: StatusNotificationService,
    private googleSheetService: GoogleSheetsService,
    private stockAnalysisService: StockAnalysisService,
    private yahoo: YahooDataService,
    private http: Http
  ) {
    this.tableViewState$.subscribe(x => {
      if (x.selectedStockList !== this.currentList) {
        this.onListSelectChange(x.selectedStockList);
      }

      this.currentList = x.selectedStockList;
    });
  }

  public getDefaultListResults() {
    this.googleSheetService.checkAuth(true, x => this.isAuthorized());
  }

  public onListSelectChange(selectedStockList: string) {
    if (!this.stockListHeaders) {
      return;
    }

    const stockListHeader: StockListHeader = this.stockListHeaders.filter(
      (x: StockListHeader) => x.title === selectedStockList
    )[0];
    this.BeginIsInProgress("GOOGLE SHEETS", "Retrieving Data...");
    this.googleSheetService
      .getSheetData(stockListHeader)
      .subscribe((companies: any) => {
        const tickers = _(companies.response)
          .filter((x: any) => x.isActive == 1)
          .uniqBy((x: any) => x.ticker)
          .value();

        this.ngRedux.dispatch({
          type: Actions.StockDashboardActions.GOOGLE_SHEET_DETAILS_RECEIVED,
          list: {
            listTitle: companies.sheetInfo.title,
            tickers: tickers
          }
        });

        this.EndIsInProgress("GOOGLE SHEETS", "Data Received");
        // this.getYahoo(tickers);
      });
  }

  private isAuthorized() {
    this.BeginIsInProgress("GOOGLE SHEETS", "Retrieving Data...");
    this.googleSheetService
      .getMasterSheetData()
      .subscribe((stockListHeaders: any) => {
        this.stockListHeaders = stockListHeaders.response;

        this.ngRedux.dispatch({
          type:
            Actions.StockDashboardActions.REQUEST_SELECTED_STOCK_LIST_SUCCESS,
          selectedStockList: this.stockListHeaders[0].title
        });

        this.ngRedux.dispatch({
          type:
            Actions.StockDashboardActions.REQUEST_STOCK_LIST_HEADERS_SUCCESS,
          stockListHeaders: this.stockListHeaders,
          tableState: new TableViewState(
            this.stockListHeaders[0].title,
            true,
            true
          )
        });

        this.onListSelectChange(this.stockListHeaders[0].title);
        this.EndIsInProgress("GOOGLE SHEETS", "Data Received");

        _.forEach(this.stockListHeaders, sheetInfo => {
          this.googleSheetService
            .getSheetData(sheetInfo)
            .subscribe(selected => {
              const tickers4Analysis = _(selected)
                .uniqBy(x => x.ticker)
                .value();

              this.ngRedux.dispatch({
                type: Actions.StockDashboardActions.SAVE_TICKERS_4_ANALYSIS,
                tickers4Analysis: tickers4Analysis
              });
            });
        });
      });
  }

  private getYahoo(tickers: CompanyInformation[]) {
    this.BeginIsInProgress("YAHOO", "Retreiving Data...");
    this.yahoo
      .getTickerData(tickers)
      .subscribe((nestedResults: YahooQuote[]) => {
        this.handleYahooResponse(nestedResults, tickers);
      });
  }

  private handleYahooResponse(
    nestedResults: YahooQuote[],
    tickers: CompanyInformation[]
  ) {
    let vixModel: VixModel = new VixModel();
    const results = [];
    _.forEach(nestedResults, (quotes: YahooQuote[]) => {
      _.forEach(quotes, (quote: YahooQuote) => {
        const category: string = tickers.filter(
          x => x.ticker === quote.Symbol
        )[0].category;
        const result: DecoratedYahooQuote = this.stockAnalysisService.decorateQuote(
          quote,
          category
        );
        result.ChangeinPercent = parseFloat(result.ChangeinPercent);
        results.push(result);
        vixModel = this.buildVixModel(vixModel, result);
      });
    });

    if (vixModel.spxDailyPercentChange) {
      this.ngRedux.dispatch({
        type: Actions.StockDashboardActions.REQUEST_VIX_MODEL_SUCCESS,
        vixModel: vixModel
      });
    }

    const groupedYahooResults: DecoratedYahooQuote[][] = _.values(
      _(results)
        .groupBy(x => x.group)
        .value()
    );
    this.EndIsInProgress("YAHOO", "Data Received");
    this.ngRedux.dispatch({
      type: Actions.StockDashboardActions.REQUEST_YAHOO_RESULTS_SUCCESS,
      groupedYahooResults: groupedYahooResults
    });
  }

  private buildVixModel(
    vixModel: VixModel,
    result: DecoratedYahooQuote
  ): VixModel {
    if (result.Symbol.endsWith(".NYM")) {
      // this will need to be updated to the new contract
      vixModel.wti = parseFloat(result.LastTradePriceOnly.toString());
      vixModel.wtiDailyPercentChange =
        parseFloat(result.ChangeinPercent.toString()) / 100;
    }
    if (result.Symbol === "SPY") {
      vixModel.spxDailyPercentChange =
        parseFloat(result.ChangeinPercent.toString()) / 100;
      vixModel.spxRelativeVolume = result.relativeVolume / 100;

      const changeAbove200ma: number = parseFloat(
        result.ChangeFromTwoHundreddayMovingAverage.toString()
      );
      const ma200: number = parseFloat(
        result.TwoHundreddayMovingAverage.toString()
      );

      vixModel.spxChangeFromTwoHundreddayMovingAverage =
        (ma200 + changeAbove200ma) / ma200 - 1;
    }
    if (result.Symbol === "DX-Y.NYB") {
      vixModel.usd = parseFloat(result.LastTradePriceOnly.toString());
      vixModel.usdDailyPercentChange =
        parseFloat(result.ChangeinPercent.toString()) / 100;
    } else if (result.Symbol === "^VIX") {
      vixModel.vix = parseFloat(result.LastTradePriceOnly.toString());
      vixModel.vixDailyPercentChange =
        parseFloat(result.ChangeinPercent.toString()) / 100;
    } else if (result.Symbol === "^VXV") {
      vixModel.vixThreeMonth = parseFloat(result.LastTradePriceOnly.toString());
    }

    return vixModel;
  }

  private BeginIsInProgress(
    source: string = "",
    message: string = "Querying...",
    duration: number = 3000
  ): void {
    this.statusNotificationService.setIsUpdating(true);
    this.statusNotificationService.openSnackBar(
      source + ":",
      message,
      duration
    );
  }

  private EndIsInProgress(
    source: string = "",
    message: string = "Querying...",
    duration: number = 3000
  ): void {
    this.statusNotificationService.setIsUpdating(false);
    this.statusNotificationService.openSnackBar(
      source + ":",
      message,
      duration
    );
  }
}



