import { Component, OnInit, ViewEncapsulation, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { AppRouteProvider, RouterAndMenuItem } from '../../../shared/routing';
import { StatusNotificationService } from '../../AppModule/services/status-notification.service';

@Component({
  selector: 'dashboard-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class NavigationComponent implements OnInit {
  public routeData: RouterAndMenuItem[];
  isInprogress: boolean;

  constructor(
    private router: Router,
    private routeSource: AppRouteProvider,
    private statusNotificationService: StatusNotificationService,
    private changeDetector: ChangeDetectorRef) { }

  ngOnInit() {
    const that = this;
    this.routeData = this.routeSource.getMenuItems();

    const defaultRoute = this.routeData.find(x => x.isDefault);

    this.statusNotificationService.statusUpdate.subscribe((x: boolean) => {
      this.isInprogress = x;
      this.changeDetector.detectChanges();
    });

    setTimeout(() => {
      that.router.navigate(['/' + defaultRoute.path]);
     }, 500);
  }
}



