import {
  Component,
  ChangeDetectionStrategy,
  OnInit,
  EventEmitter,
  Output
} from "@angular/core";
import { DecoratedYahooQuote } from "../../../../shared";
import { select } from "@angular-redux/store";
import { Observable } from "rxjs/Observable";

import * as _ from "lodash";

@Component({
  selector: "chart-viewer",
  templateUrl: "./chart-viewer.component.html",
  styles: [],
  changeDetection: ChangeDetectionStrategy.Default
})
export class ChartViewerComponent implements OnInit {
  @select("groups") public groups$: Observable<any>;
  @select("tableState") public tableState$: Observable<any>;
  @Output() onChange: EventEmitter<any> = new EventEmitter(true);
  selectedList: string;
  groups: any;
  selectedGroup: any;
  arrayOfKeys: any;

  ngOnInit(): void {
    this.groups$.subscribe(x => {
      this.groups = x;

      const tmp = _.filter(
        this.groups,
        (x: any) => x.title === this.selectedList
      )[0];

      if (tmp && tmp.tickers) {
        this.selectedGroup = _.groupBy(tmp.tickers, (x: any) => x.category);
        this.arrayOfKeys = Object.keys(this.selectedGroup);
        this.onChange.emit(this.selectedGroup);
      }

      console.log(this.selectedGroup);
    });

    this.tableState$.subscribe(x => {
      this.selectedList = x.selectedStockList;
    });
  }

  rowClicked(result: any) {
    const win = window.open(
      `http://www.gurufocus.com/stock/${result.ticker}`,
      "_blank"
    );
    const win2 = window.open(
      `http://quote.morningstar.com/Quote.html?ticker=${result.ticker}`,
      "_blank"
    );
    const win3 = window.open(
      `https://www.tradingview.com/chart/?symbol=${result.ticker}&interval=D&hidesidetoolbar=0&symboledit=1&toolbarbg=f4f7f9&studies=&hideideas=0&theme=Black&timezone=exchange&showpopupbutton=1&studies_overrides=%7B%7D&overrides=%7B%7D&enabled_features=%5B%5D&disabled_features=%5B%5D&showpopupbutton=1`,
      "_blank"
    );
  }
}



