import * as _ from "lodash";

import { BaseReducer } from "../../shared/redux/BaseReducer";
import { Actions } from "../../shared/redux/actions";
import { IAppState } from "../../shared/redux/IAppState";
import { DecoratedYahooQuote } from "../../shared";

export class StockDashboardReducer extends BaseReducer {
  public reducer(state, action): IAppState {
    switch (action.type) {
      case Actions.StockDashboardActions.GOOGLE_SHEET_DETAILS_RECEIVED:
        return this.googleSheetDetailsReceived(state, action);
      case Actions.StockDashboardActions.REQUEST_SELECTED_STOCK_LIST_SUCCESS:
        return this.updateSelectedStockList(state, action);
      case Actions.StockDashboardActions.REQUEST_STOCK_LIST_HEADERS_SUCCESS:
        return this.updateStockListHeaders(state, action);
      case Actions.StockDashboardActions.REQUEST_VIX_MODEL_SUCCESS:
        return this.updateVixModel(state, action);
      case Actions.StockDashboardActions.REQUEST_YAHOO_RESULTS_SUCCESS:
        return this.updateGroupedYahooResults(state, action);
      case Actions.StockDashboardActions.TABLE_VIEW_STATE_CHANGE:
        return this.updateTableState(state, action);
      case Actions.StockDashboardActions.TABLE_SORT_CHANGE:
        return this.updateTableSort(state, action);
      case Actions.StockDashboardActions.SAVE_TICKERS_4_ANALYSIS:
        return this.saveTickers4Analysis(state, action);
      default:
        return state;
    }
  }

  private saveTickers4Analysis(state, action): IAppState {
    return this.updateObject(state, {
      tickers4Analysis: action.tickers4Analysis
    });
  }

  private updateTableSort(state, action): IAppState {
    const result: DecoratedYahooQuote[][] = [];
    _.map(state.groupedYahooResults, (group: DecoratedYahooQuote[]) => {
      let tmpgroup = _.sortBy(group, x => x[action.sortBy]);

      if (state.sortBy === action.sortBy) {
        tmpgroup = tmpgroup.reverse();
      }

      result.push(tmpgroup);
    });

    return this.updateObject(state, {
      sortBy: action.sortBy,
      groupedYahooResults: result
    });
  }

  private updateTableState(state, action): IAppState {
    return this.updateObject(state, {
      tableState: { ...action.tableState, showTable: false }
    });
  }

  private updateGroupedYahooResults(state, action): IAppState {
    return this.updateObject(state, {
      groupedYahooResults: action.groupedYahooResults
    });
  }

  private updateVixModel(state, action): IAppState {
    return this.updateObject(state, {
      vixModel: action.vixModel
    });
  }

  private updateStockListHeaders(state, action): IAppState {
    return this.updateObject(state, {
      stockListHeaders: action.stockListHeaders,
      tableState: { ...action.tableState, showTable: false }
    });
  }

  private updateSelectedStockList(state, action): IAppState {
    return this.updateObject(state, {
      selectedStockList: action.selectedStockList
    });
  }

  private googleSheetDetailsReceived(state, action: any): IAppState {
    let tmp = [];

    if (state.groups) {
      tmp = _.cloneDeep(state.groups);
    }

    if (!_.some(tmp, (x: any) => x.title === action.list.listTitle)) {
      tmp.push({
        title: action.list.listTitle,
        tickers: _.filter(action.list.tickers, (x: any) => x.isActive === "1")
      });
    }

    return {
      ...state,
      groups: tmp
    };
  }
}



