import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ChangeDetectionStrategy,
  ChangeDetectorRef
} from "@angular/core";
import { FormControl } from "@angular/forms";
import * as _ from "lodash";
import { Observable } from "rxjs/Observable";

@Component({
  selector: "auto-select",
  templateUrl: "./auto-select.component.html",
  styleUrls: ["./auto-select.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AutoSelectComponent {
  @Input() placeholder: string;
  @Input() type: string;
  @Input() list: any[];
  @Input() getKey: Function;
  @Input() getDescription: Function;
  @Output() onChange: EventEmitter<any> = new EventEmitter(true);

  formControl: FormControl;
  filteredList: Observable<any[]>;

  set selectedValue(value) {
    this.onChange.emit(value);
  }

  constructor(private cd: ChangeDetectorRef) {
    this.formControl = new FormControl();
    this.filteredList = this.formControl.valueChanges.startWith(null).map(
      enteredText =>
        enteredText
          ? this.list.filter(item => {
              return _.includes(
                this.getDescription(item).toLowerCase(),
                typeof enteredText === "string"
                  ? enteredText.toLowerCase()
                  : this.getDescription(enteredText)
              );
            })
          : this.list
    );
  }
}



